import React from "react";
import { useNavigate, useParams } from "react-router-dom";
import tw from "tailwind-styled-components";
import Arrowleft from "../../assets/images/arrow-left.svg";
import Arrowright from "../../assets/images/arrow-right.svg";

const Container = tw.div`fixed left-0 bottom-0 w-full z-50 border-3 border-indigo-900 bg-gray-50`;
const Info = tw.div`flex w-full items-center`;
const Button = tw.div`flex`;

function Navigator() {
  const { id, chID } = useParams();
  const navigate = useNavigate();
  const nextCh = Number(chID) + 1;
  const prevCh = Number(chID) - 1;

  return (
    <Container>
      <div class="container flex flex-wrap mx-auto px-4 py-2">
        <div class="flex my-auto items-center w-full lg:w-1/2 md:w-2/3 sm:w-3/5 px-2 lg:md-0">
          <Info>
            <button class="navbar-burger focus:outline-none text-indigo-900 hover:text-indigo-800">
              <svg
                class="block h-6 w-6 md:mr-4 mr-2"
                fill="black"
                viewbox="0 0 20 20"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z"></path>
              </svg>
            </button>
            <h2 class="text-xl md:text-2xl lg:text-3xl md:mr-6 mr-2 text-indigo-900 font-heading">
              Lesson {id}
            </h2>
            <h3 class="text-lg md:text-1xl md:mr-6 mr-2 text-orange-400 font-heading">
              {chID}/5
            </h3>
          </Info>
          <Button>
            <button
              class=" inline-block border-3 rounded-full border-blue-500 hover:bg-yellow-500 py-1  md:mt-0 px-4  md:text-lg text-sm font-extrabold  text-blue-500"
              href="#"
            >
              Check your Answer!
            </button>
            <button
              class="hidden marker:inline-block border-3 rounded-full border-red-500 hover:bg-red-200 py-1 md:mt-0 px-4  md:text-lg text-sm font-extrabold  text-red-500"
              href="#"
            >
              Wrong! Try again
            </button>
            <button
              class="hidden border-3 rounded-full border-green-500 hover:bg-green-200 py-1  md:mt-0 px-4  md:text-lg text-sm font-extrabold  text-green-500"
              href="#"
            >
              Correct! Next Lesson
            </button>
          </Button>
        </div>

        <div class="w-3/6 lg:w-1/2 md:w-1/3 sm:w-2/5 md:px-4 px-2">
          <div class="flex flex-wrap items-center justify-end">
            <button onClick={() => navigate(`/lesson/${id}/chapter/${prevCh}`)}>
              <img
                class="inline-block md:mr-6 bg-green-500 border-3 border-indigo-900 shadow rounded transition duration-200 md:py-4 py-2 md:px-5 px-3 mr-2"
                src={Arrowleft}
                alt=""
                href="#"
              />
            </button>
            <button onClick={() => navigate(`/lesson/${id}/chapter/${nextCh}`)}>
              <img
                class="inline-block md:w-auto md:my-0 md:mr-6 bg-blue-500 border-3 border-indigo-900 shadow rounded transition duration-200 md:px-5 px-3 py-2 md:py-4 mr-2"
                src={Arrowright}
                alt=""
                href="#"
              />
            </button>
          </div>
        </div>
      </div>
    </Container>
  );
}

export default Navigator;
