import { Route, Routes } from "react-router";
import Chapter from "./components/main/chapter/Chapter";
import Home from "./components/main/Home";
import Lesson from "./components/main/lesson/Lesson";

function App() {
  return (
    <div>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/lesson/:id" element={<Lesson />} />
        <Route path="/lesson/:id/chapter/:chID" element={<Chapter />} />
      </Routes>
    </div>
  );
}

export default App;
